import { Renderer } from "../renderer/Renderer";

export const Initialize = async () => {

    const canvas = document.querySelector("canvas#gfx-main") as HTMLCanvasElement; // `as HTMLCanvasElement`   
    if (!canvas) {
        console.error("Canvas element not found");
        return;
    }

    let isLeftMouseDown = false;
    let isRightMouseDown = false;
    let lastMouseX: number, lastMouseY: number;

    canvas.addEventListener('mousedown', (event: MouseEvent) => { // `MouseEvent`  
        if (event.button === 0) { // 
            isLeftMouseDown = true;
            console.log("");
        } else if (event.button === 2) { // 
            isRightMouseDown = true;
            console.log("");
        }
        lastMouseX = event.clientX;
        lastMouseY = event.clientY;

    });

    document.addEventListener('mouseup', (event) => {
        isLeftMouseDown = false;
        isRightMouseDown = false;
    });

    const sceneManager = new Renderer("gfx-main");
    sceneManager.init().then(() => {

        canvas.addEventListener('mousemove', (event) => {
            if (isLeftMouseDown) {
                //    
                const dx = event.clientX - lastMouseX;
                const dy = event.clientY - lastMouseY;
                //console.log("rotate");
                sceneManager.rotateCamera(dx, dy);

            } else if (isRightMouseDown) {
                //    
                const dx = event.clientX - lastMouseX;
                const dy = event.clientY - lastMouseY;

                //   
                sceneManager.panCamera(dx, dy);
            }
            lastMouseX = event.clientX;
            lastMouseY = event.clientY;
        });

        canvas.addEventListener('wheel', (event) => {
            //   /  
            sceneManager.zoomCamera(event.deltaY / 100);
        });
        animate();
    });

    function animate() {
        sceneManager.render();
        requestAnimationFrame(animate);
    }
}